<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe="">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status"></el-table-column>
        <el-table-column label="" prop="is_send">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              @click="showBox"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              @click="showProgressBox"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
    <!--  -->

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="Closed"
    >
      <el-form
        :model="addressForm"
        :rules="addressrules"
        ref="addressForm"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <!--  cityData vmaddressForm -->
          <el-cascader
            :props="{ expandTrigger: 'hover' }"
            :options="cityData"
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>

    <!--  -->

    <el-dialog title="" :visible.sync="ProgressVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
          placement="top"
        >
          <el-card
            ><span>{{ activity.context }}</span></el-card
          >
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from "./citydata";
import { getOrdersList, getProgress } from "../../api";
export default {
  name: "Order",
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      //   
      total: 0,
      // 
      orderList: [],
      dialogVisible: false,
      ProgressVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: "",
      },
      addressrules: {
        address1: [
          { required: true, message: "", trigger: "blur" },
        ],
        address2: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      progressInfo: [],
      //   
      cityData,
    };
  },
  methods: {
    // 
    getOrderList() {
      getOrdersList({ params: this.queryInfo }).then(({ data }) => {
        // console.log(data);
        if (data.meta.status !== 200) {
          return this.$message.error("!");
        }
        this.total = data.data.total;
        this.orderList = data.data.goods;
      });
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOrderList();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getOrderList();
    },
    // 
    showBox() {
      this.dialogVisible = true;
    },
    // 
    Closed() {
      this.$refs.addressForm.resetFields();
    },
    // 
    showProgressBox() {
      //   getProgress().then(({ data }) => {
      //     if (data.meta.status !== 200) {
      //       return this.$message.error("!");
      //     }
      //  
      this.progressInfo = [
        {
          time: "2018-05-10 09:39:00",
          ftime: "2018-05-10 09:39:00",
          context: ",,",
          location: "",
        },
        {
          time: "2018-05-10 08:23:00",
          ftime: "2018-05-10 08:23:00",
          context:
            "  95338",
          location: "",
        },
        {
          time: "2018-05-10 07:32:00",
          ftime: "2018-05-10 07:32:00",
          context: " ",
          location: "",
        },
        {
          time: "2018-05-10 02:03:00",
          ftime: "2018-05-10 02:03:00",
          context:
            ", ",
          location: "",
        },
        {
          time: "2018-05-09 23:05:00",
          ftime: "2018-05-09 23:05:00",
          context: " ",
          location: "",
        },
        {
          time: "2018-05-09 21:21:00",
          ftime: "2018-05-09 21:21:00",
          context:
            ", ",
          location: "",
        },
        {
          time: "2018-05-09 13:07:00",
          ftime: "2018-05-09 13:07:00",
          context: " ",
          location: "",
        },
        {
          time: "2018-05-09 12:25:03",
          ftime: "2018-05-09 12:25:03",
          context: "",
          location: "",
        },
        {
          time: "2018-05-09 12:22:24",
          ftime: "2018-05-09 12:22:24",
          context: "HLA",
          location: "",
        },
        {
          time: "2018-05-08 21:36:04",
          ftime: "2018-05-08 21:36:04",
          context: "",
          location: "",
        },
      ];
      this.ProgressVisible = true;
    },
  },
  created() {
    this.getOrderList();
  },
};
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>